.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  text-align: center;
  border: 3px solid #3a07e5;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border: 3px solid #f5e211;
  border-color: #f5e211 rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

.breed-select {
  margin: 20px;
}

.cat-info {
  width: 400px;
  margin-left: 20px;
}

.temperament, h2 {
  color: brown;
}

